<template>
  <v-container fluid :class="$vuetify.breakpoint.mdAndUp ? 'pa-0' : ''">
    <tool-bar></tool-bar>
    <v-row>
      <v-col cols="12" lg="6">
        <app-layout title="New Category">
          <v-col v-for="n in 6" :key="n" cols="12" md="6">
            <category-card
              isHoverAndRipple
              title="Landscape design"
              subTitle="Greyhound divisely hello coldly fonwderfully"
              :id="n.toString()"
            >
            </category-card>
          </v-col>
        </app-layout>
      </v-col>
      <v-col cols="12" lg="6">
        <categories-all></categories-all>
      </v-col>
    </v-row>
    <v-alert dense type="error" class="alert-fix" :value="isError">
      <strong> Connection failed.</strong> Try next time -- {{error}}
      <v-btn icon color="white" right @click.stop="closeAlert()">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-alert>
  </v-container>
</template>

<script>
import axios from 'axios'

import AppLayout from '../components/AppLayout.vue'
import toolBar from '../components/TheToolBarHome.vue'

// const PATH_IMG = 'https://rest.jobhunter.rest/public/uploads/'

export default {
  name: 'categories-page',
  components: {
    'app-layout': AppLayout,
    'categories-all': () => import('./CategoriesAll.vue'),
    'category-card': () => import('../components/CategoryCard.vue'),
    'tool-bar': toolBar
  },
  data () {
    return {
      loading: true,
      error: '',
      page: 1,
      data: []
    }
  },
  computed: {
    isError: function () {
      return this.error !== ''
    }
  },
  created: function () {
    // console.log(this.avatar)
  },
  mounted: function () {
    this.fetchData()
  },
  methods: {
    fetchData: function () {
      axios
        .get('/categories', {
          params: {
            limit: 12,
            page: 1
          }
        })
        .then(response => {
          this.data = response.data.data
          // console.log(this.data)
          this.loading = false
        })
        .catch(error => {
          console.error(error)
          this.error = error
          this.loading = true
        })
    },
    toMovement: function () {
      this.$router.push({
        path: '/view',
        params: { id: this.movementId }
      })
    },
    closeAlert: function () {
      this.error = ''
    }
  }
}
</script>

<style lang="scss" scoped>
.alert-fix {
  position: fixed;
  bottom: 0;
  left: 50%;
  width: 90%;
  z-index: 10;
  transform: translateX(-50%);
}
</style>
